import React, { useEffect }  from 'react'
import { motion } from 'framer-motion'

import { HeroContainer, Title } from '../../styles/pageTitleStyles'
import { Container } from '../../styles/globalStyles'

// Context
// import {useGlobalStateContext, useGlobalDispatchContext} from '../../context/globalContext'
//Scroll Behavior
import {useInView} from 'react-intersection-observer'
import {useAnimation} from 'framer-motion'

const HomeTitle = (props) => {

    const animation = useAnimation();
    const [contentRef, inView] = useInView({
        triggerOnce: true,
    })

    useEffect(() => {
        if(inView) {
            animation.start('visible');
        }
    }, [animation, inView])

    // const parent = {
    //     initial: {
    //         y: 800
    //     },
    //     animate: {
    //         y: 0,
    //         transition: {
    //             staggerChildren: 0.2,
                
    //         }
    //     }
    // }

    // const child = {
    //     initial: {
    //         y: 800
    //     },
    //     animate: {
    //         y: 0,
    //         transition: {
    //             duration: 1,
    //             ease: [0.6, 0.05, -0.01, 0.9]
    //         }
    //     }
    // }

    return(
        <HeroContainer>
            <Container>
                {/* <Title variants={parent} initial='initial' animate="animate">  */}
                <Title 
                    ref={contentRef} 
                    animate={animation} 
                    initial='hidden' 
                    variants={
                        {hidden: {
                            opacity: 0,
                            y: 72
                        }, 
                        visible: {
                            opacity: 1,
                            y:0,
                            transition: {
                                duration: 0.6, 
                                ease: [0.6, 0.05, -0.01, 0.9]
                                }
                            }
                        }
                    }>
                    <p>{props.leading}</p>
                    <h1>{props.title}</h1>
                    {/* <motion.p variants={child}>{props.leading}</motion.p>
                    <motion.h1 variants={child}>{props.leading}</motion.h1> */}
                    
                    {/* <motion.h1 variants={parent} initial='initial' animate="animate">
                    
                        {props.title.split(" ").map((e) => {
                            return <motion.span variants={child}>{e + " "}</motion.span>
                        })}
                    </motion.h1> */}
                    
                </Title>
                
                
            </Container>
            
            
            
           
            
        </HeroContainer>
    );
}

export default HomeTitle;