import React, { useEffect } from 'react'

import { ContactContainer, FormGroup } from '../../styles/contactUsStyles'
import { Container, Button, DefaultSizes, VerticalPadding } from '../../styles/globalStyles'

// Context
// import {useGlobalStateContext, useGlobalDispatchContext} from '../../context/globalContext'
//Scroll Behavior
import {useInView} from 'react-intersection-observer'
import {useAnimation} from 'framer-motion'



const Contact = () => {

    const animation = useAnimation();
    const [contentRef, inView] = useInView({
        triggerOnce: true,
        rootMargin: '-100px',
    })

    useEffect(() => {
        if(inView) {
            animation.start('visible');
        }
    }, [animation, inView])

    return(
        <ContactContainer>
            <Container 
                ref={contentRef} 
                animate={animation} 
                initial='hidden' 
                variants={
                    {hidden: {
                        opacity: 0,
                        y: 72
                    }, 
                    visible: {
                        opacity: 1,
                        y:0,
                        transition: {
                            duration: 0.6, 
                            ease: [0.6, 0.05, -0.01, 0.9]
                            }
                        }
                    }
                }>
                <VerticalPadding>
                    <DefaultSizes black>
                        <h1>Contacte-nos!</h1>
                        <p>Entre em contacto connosco e responderemos o mais rapidamente possível.</p>
                    </DefaultSizes>
                
                
                

                <form>
                    <FormGroup>
                        <label htmlFor="name">Nome</label>
                        <input type="text" id="name"/>
                    </FormGroup>
                    <FormGroup className="form-group">
                        <label htmlFor="email">Email</label>
                        <input type="text" id="email"/>
                    </FormGroup>
                    <FormGroup className="form-group">
                        <label htmlFor="message">Mensagem</label>
                        <textarea type="text" id="message"></textarea>
                    </FormGroup>
                    <Button>
                        <button id="new-email">Enviar</button>
                    </Button>
                    
                    
                </form>

                </VerticalPadding>
            </Container>
            
            
        </ContactContainer>
    );
}

export default Contact;