
import React, {useEffect} from 'react'

import { Video, BriefContainer, BriefBackground } from '../../styles/videoCaptionStyles'
import { Container, DefaultSizes, VerticalPadding } from '../../styles/globalStyles'

// Context
// import {useGlobalStateContext, useGlobalDispatchContext} from '../../context/globalContext'

//Scroll Behavior
import {useInView} from 'react-intersection-observer'
import {useAnimation} from 'framer-motion'

const VideoCaption = (props) => {

    const animation = useAnimation();
    const [contentRef, inView] = useInView({
        triggerOnce: true,
        rootMargin: '-100px',
    })

    useEffect(() => {
        if(inView) {
            animation.start('visible');
        }
    }, [animation, inView])

    return(
        <BriefBackground>
            <Video>
                <video height='100%' width='100%' muted loop autoPlay src={props.video} ></video>
            </Video>
            <Container>
                {/* <Video>
                    <video height='100%' width='100%' muted loop autoPlay src={require('../../assets/video/coding.mp4')} ></video>
                </Video> */}
                
                <VerticalPadding>
                    <BriefContainer
                        // animate={{y:0, opacity: 1}}
                        // initial={{y:-72, opacity: 0}}
                        // transition={{duration: 1, ease: [.6, .05, -.01, 0.9], delay:0.5}}
                        ref={contentRef} 
                        animate={animation} 
                        initial='hidden' 
                        variants={
                            {hidden: {
                                opacity: 0,
                                y: 72
                            }, 
                            visible: {
                                opacity: 1,
                                y:0,
                                transition: {
                                    duration: 0.6, 
                                    ease: [0.6, 0.05, -0.01, 0.9]
                                    }
                                }
                            }
                        }
                    >
                        <DefaultSizes>
                            <h1>{props.caption}</h1>
                        </DefaultSizes>
                        
                    </BriefContainer>
                </VerticalPadding>
                
                
                
                
            </Container>
        </BriefBackground>
        
    )
}

export default VideoCaption